    [
      {
        "id":1,
        "paragraph":"Donec venenatis est non tristique dignissim. Etiam vitae turpis ac tortor molestie vehicula. Aliquam quis neque elementum, varius lectus eget, ultricies turpis. Quisque sed volutpat neque, eu finibus mauris. Aliquam tempus, nulla non maximus ultricies, magna elit ullamcorper tortor, et dapibus ante enim vitae mauris. Proin mattis fermentum sem vel dapibus. Duis in consectetur enim. Donec vestibulum odio ac facilisis gravida. Fusce cursus, diam sit amet finibus eleifend, quam nunc laoreet sapien, vitae lobortis diam turpis at ex. Vestibulum ut elit pretium, suscipit nisi quis, luctus mauris. Aliquam eget eros a nisl lobortis iaculis. Pellentesque aliquet dapibus ipsum quis tempor. Nulla nec risus et massa vestibulum pretium. Duis ex tellus, euismod in vestibulum nec, fringilla ac magna. Mauris suscipit ullamcorper tempor.",
        "title":"Atlar",
        "defaultSpeed":300,
        "author":{
          "id":2,
          "name":"mehmed",
          "icon":"thisisanicon"
        }
      },
      {
        "id":12,
        "paragraph":"blah blah blah",
        "title":"Filler",
        "defaultSpeed":200,
        "author":{
          "id":2,
          "name":"akif",
          "icon":"yesthisisanothericon"
        }
      },
      {
        "id":13,
        "paragraph":"blah blah blah",
        "title":"Filler",
        "defaultSpeed":200,
        "author":{
          "id":2,
          "name":"akif",
          "icon":"yesthisisanothericon"
        }
      },
      {
        "id":14,
        "paragraph":"blah blah blah",
        "title":"Filler",
        "defaultSpeed":200,
        "author":{
          "id":2,
          "name":"akif",
          "icon":"yesthisisanothericon"
        }
      }
    ]


function Container(){
    const {theme} = useTheme('dark');
    const [paragraphs, setParagraphs] = useState([]);

    React.useEffect(() => {
        setParagraphs(staticData)
        //cardAPI.get("/paragraphs").then(response=>{setParagraphs(staticData)})
      }, []);


    function GetGrid (){
        var elements = []
        paragraphs.forEach(element => elements.push(
                <Col key={element.id}>
                    <WordCard
                        _paragraph={element.paragraph}
                        title={element.title} 
                        defaultSpeed={element.defaultSpeed}/>
                    <br></br>
                </Col>
            ));
        return(
            <Row justify="space-around" >
               {elements}
            </Row>
        )
    }


    return(
        <Layout.Content>
            {GetGrid()}
        </Layout.Content>
    )
}

export default Container;